import React, { Component } from "react";
import "./User.scss";
import { handleUpload } from "../../service/ImportFileCSV";
import PrivatePage from "../../HOC/PrivatePage";
import { toast } from "react-toastify";
import Papa from "papaparse";
import Table from "react-bootstrap/Table";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listUser: [],
      file: [],
    };
  }

  handleUploadFile = (event) => {
    const self = this;
    if (event.target && event.target.files && event.target.files[0]) {
      let file = event.target.files[0];
      this.setState({
        file: file,
      });

      if (file.type !== "text/csv") {
        toast.error("Only accept csv file");
        return;
      }

      Papa.parse(file, {
        //header: true,
        complete: function (result) {
          console.log(result.data);
          let rawCSV = result.data;
          if (rawCSV.length > 0) {
            console.log(rawCSV[0].length);
            if (rawCSV[0] && rawCSV[0].length === 31) {
              const results = [];
              rawCSV.map((item, index) => {
                if (index > 0 && item.length === 31) {
                  let obj = {};
                  obj.Time = item[0];
                  obj.SampleCount = item[1];
                  obj.GreenCount = item[2];
                  obj.Green2Count = item[3];
                  obj.IRCount = item[4];
                  obj.RedCount = item[5];
                  obj["XAxisAcceleration(g)"] = item[6];
                  obj["YAxisAcceleration(g)"] = item[7];
                  obj["ZAxisAcceleration(g)"] = item[8];
                  obj["OperatingMode"] = item[9];
                  obj["HeartRate(bpm)"] = item[10];
                  obj["HRConfidence(%)"] = item[11];
                  obj["RR"] = item[12];
                  obj["RRConfidence(%)"] = item[13];
                  obj["Activity"] = item[14];
                  obj["RValue"] = item[15];
                  obj["SpO2Confidence(%)"] = item[16];
                  obj["SpO2(%)"] = item[17];
                  obj["SpO2PercentComplete"] = item[18];
                  obj["LowSignalQuality"] = item[19];
                  obj["MotionFlag"] = item[20];
                  obj["WSPO2LowPi"] = item[21];
                  obj["UnreliableR"] = item[22];
                  obj["SpO2State"] = item[23];
                  obj["SCDState"] = item[24];
                  obj["SAMPLETime"] = item[25];
                  obj["WalkSteps"] = item[26];
                  obj["RunSteps"] = item[27];
                  obj["KCal"] = item[28];
                  obj["Tot.Act.Energy"] = item[29];
                  obj["IbiOffset"] = item[30];
                  results.push(obj);
                }
              });

              self.setState({
                listUser: results,
              });
            } else {
              toast.error("Wrong format CSV!");
            }
          } else {
            toast.error("Not found CSV file!");
          }
        },
      });
    }
  };

  handleSaveFile = async (event) => {
    try {
      let file = this.state.file;
      console.log("check: ", file);
      if (file.type !== "text/csv") {
        toast.error("Only accept csv file");
        return;
      } else {
        const formData = new FormData();
        formData.append("uploadfile", file);
        await handleUpload(formData);
        toast.success("Uploaded file successfully");
      }
    } catch (e) {
      console.log(e);
    }
  };

  render() {
    let users = this.state.listUser;
    return (
      <>
        <div className="user-container">
          <b>User Dashboard</b>
          <br></br>
          <label htmlFor="test" className="btn btn-primary">
            <i class="fa-solid fa-file-import"></i> Import File
          </label>
          &nbsp;
          <label
            className="btn btn-success"
            onClick={(event) => this.handleSaveFile(event)}
          >
            <i class="fa-solid fa-floppy-disk"></i> Save File
          </label>
          <input
            id="test"
            type="file"
            name="uploadfile"
            hidden
            onChange={(event) => this.handleUploadFile(event)}
          ></input>
        </div>
        <br></br>
        <div className="table-container">
          <Table
            striped
            bordered
            responsive
            hover
            variant="success"
            id="customers"
          >
            <thead>
              <tr>
                <th>Time</th>
                <th>Sample Count</th>
                <th>Green Count</th>
                <th>Green2 Count</th>
                <th>IR Count</th>
                <th>Red Count</th>
                <th>X Axis Acceleration (g)</th>
                <th>Y Axis Acceleration (g)</th>
                <th>Z Axis Acceleration (g)</th>
                <th>Operating Mode</th>
                <th>Heart Rate (bpm)</th>
                <th>HR Confidence (%)</th>
                <th>RR</th>
                <th>RR Confidence (%)</th>
                <th>Activity</th>
                <th>R Value</th>
                <th>SpO2 Confidence (%)</th>
                <th>SpO2 (%)</th>
                <th>SpO2 Percent Complete</th>
                <th>Low Signal Quality</th>
                <th>Motion Flag</th>
                <th>WSPO2 Low Pi</th>
                <th>Unreliable R</th>
                <th>SpO2 State</th>
                <th>SCD State</th>
                <th>SAMPLE Time</th>
                <th>Walk Steps</th>
                <th>Run Steps</th>
                <th>KCal</th>
                <th>Tot. Act. Energy</th>
                <th>Ibi Offset</th>
              </tr>
            </thead>
            <tbody>
              {users &&
                users.length > 0 &&
                users.map((item, index) => {
                  return (
                    <tr>
                      <td>{item["Time"]}</td>
                      <td>{item["SampleCount"]}</td>
                      <td>{item["GreenCount"]}</td>
                      <td>{item["Green2Count"]}</td>
                      <td>{item["IRCount"]}</td>
                      <td>{item["RedCount"]}</td>
                      <td>{item["XAxisAcceleration(g)"]}</td>
                      <td>{item["YAxisAcceleration(g)"]}</td>
                      <td>{item["ZAxisAcceleration(g)"]}</td>
                      <td>{item["OperatingMode"]}</td>
                      <td>{item["HeartRate(bpm)"]}</td>
                      <td>{item["HRConfidence(%)"]}</td>
                      <td>{item["RR"]}</td>
                      <td>{item["RRConfidence(%)"]}</td>
                      <td>{item["Activity"]}</td>
                      <td>{item["RValue"]}</td>
                      <td>{item["SpO2Confidence(%)"]}</td>
                      <td>{item["SpO2(%)"]}</td>
                      <td>{item["SpO2PercentComplete"]}</td>
                      <td>{item["LowSignalQuality"]}</td>
                      <td>{item["MotionFlag"]}</td>
                      <td>{item["WSPO2LowPi"]}</td>
                      <td>{item["UnreliableR"]}</td>
                      <td>{item["SpO2State"]}</td>
                      <td>{item["SCDState"]}</td>
                      <td>{item["SAMPLETime"]}</td>
                      <td>{item["WalkSteps"]}</td>
                      <td>{item["RunSteps"]}</td>
                      <td>{item["KCal"]}</td>
                      <td>{item["Tot.Act.Energy"]}</td>
                      <td>{item["IbiOffset"]}</td>
                    </tr>
                  );
                })}
            </tbody>
          </Table>
        </div>
      </>
    );
  }
}

export default PrivatePage(Home);
