import React, { Component } from "react";
import Button from "react-bootstrap/Button";

const mimeType = 'video/webm; codecs="opus,vp8"';

class VideoRecorder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      permission: false,
      mediaRecorder: null,
      liveVideoFeed: null,
      recordingStatus: "inactive",
      stream: null,
      recordedVideo: null,
      videoChunks: [],
    };
  }

  getCameraPermission = async () => {
    this.setState({
      recordedVideo: null,
    });

    if ("MediaRecorder" in window) {
      try {
        const videoConstraints = {
          audio: false,
          video: true,
        };
        const audioConstraints = { audio: true };

        const audioStream = await navigator.mediaDevices.getUserMedia(
          audioConstraints
        );
        const videoStream = await navigator.mediaDevices.getUserMedia(
          videoConstraints
        );

        this.setState({
          permission: true,
        });

        const combinedStream = new MediaStream([
          ...videoStream.getVideoTracks(),
          ...audioStream.getAudioTracks(),
        ]);

        this.setState({
          stream: combinedStream,
        });

        this.state.liveVideoFeed.srcObject = videoStream;
      } catch (err) {
        alert(err.message);
      }
    } else {
      alert("The MediaRecorder API is not supported in your browser.");
    }
  };

  startRecording = async () => {
    this.setState({
      recordingStatus: "recording",
    });

    const media = new MediaRecorder(this.state.stream, { mimeType });

    this.setState({
      mediaRecorder: media,
    });

    media.start();

    let localVideoChunks = [];

    media.ondataavailable = (event) => {
      if (typeof event.data === "undefined") return;
      if (event.data.size === 0) return;
      localVideoChunks.push(event.data);
    };

    this.setState({
      videoChunks: localVideoChunks,
    });
  };

  stopRecording = () => {
    this.setState({
      permission: false,
      recordingStatus: "inactive",
    });

    this.state.mediaRecorder.stop();

    this.state.mediaRecorder.onstop = () => {
      const videoBlob = new Blob(this.state.videoChunks, { type: mimeType });
      const videoUrl = URL.createObjectURL(videoBlob);

      this.setState({
        recordedVideo: videoUrl,
        videoChunks: [],
      });

      const tracks = this.state.stream.getTracks();
      tracks.forEach((track) => {
        track.stop();
      });
    };
  };

  render() {
    const { permission, recordingStatus, recordedVideo } = this.state;

    return (
      <div>
        <h2>Video Recorder</h2>
        <main>
          <div className="video-controls">
            {!permission ? (
              <Button variant="warning" onClick={this.getCameraPermission}>Get Camera</Button>
            ) : null}
            {permission && recordingStatus === "inactive" ? (
              <Button variant="primary" onClick={this.startRecording}>Start Recording</Button>
            ) : null}
            {recordingStatus === "recording" ? (
              <Button variant="success" onClick={this.stopRecording}>Stop Recording</Button>
            ) : null}
          </div>
        </main>

        <div className="video-player">
          {!recordedVideo ? (
            <video
              ref={(video) => (this.state.liveVideoFeed = video)}
              autoPlay
              className="live-player"
            ></video>
          ) : null}
          {recordedVideo ? (
            <div className="recorded-player">
              <video className="recorded" src={recordedVideo} controls></video>
              <a download href={recordedVideo}>
              <Button variant="info" style={{ marginTop: "20px" }}>Download Recording</Button>
              </a>
            </div>
          ) : null}
        </div>
      </div>
    );
  }
}

export default VideoRecorder;
