import React, { Component } from "react";
import "./Home.scss";

class Home extends Component {
  render() {
    const spanStyle = {
      color: "#446084",
      fontSize: "30px",
      fontWeight: "600",
    };
    return (
      <div className="home-body-container">
        <div className="home-body-content">
          <div className="title-body">
            Ứng dụng quản lí theo dõi sức khoẻ thông qua Smart watch
          </div>
          <div className="content-body">
            <h3>
              <span style={spanStyle}>Đáp ứng</span> nhu cầu theo dõi sức khoẻ cá nhân một cách linh động và thuận tiện
            </h3>
            <h3>
              <span style={spanStyle}>Hỗ trợ</span> các chuyên gia và bác sĩ trong công tác nghiên cứu 
            </h3>
            <h3>
              <span style={spanStyle}>Giám sát</span> và{" "}
              <span style={spanStyle}>phát hiện</span> sớm các thay đổi bất thường của cơ thế một cách dễ dàng và nhanh chóng
            </h3>
            <div className="content-body-details">
              <button className="detail">
                Xem chi tiết <i class="fa-solid fa-arrow-right"></i>
              </button>
              <button className="introduce">
                <i class="fa-solid fa-video"></i> Video giới thiệu
              </button>
            </div>
            <div className="content-body-feature">
              <div className="feature-content">
                <span className="feature-hot">Tính năng </span>nổi bật
                <p>
                  Phần mềm quản lý bảo trì trang thiết bị SpeedMaint Cloud giúp
                  Doanh nghiệp số hóa quản lý toàn diện các hoạt động liên quan
                  đến sử dụng và bảo trì thiết bị hiện tại của Bộ phận quản lý
                  bảo trì thiết bị với các bộ phận vận hành sử dụng thiết bị, bộ
                  phận kho, kế toán,... trên cùng một hệ thống phần mềm trực
                  tuyến, nhằm giảm thiểu rủi ro hỏng hóc thiết bị đột xuất và
                  giảm thời gian xử lý sự cố, sửa chữa thiết bị, từ đó đảm bảo
                  ổn định hoạt động sản xuất kinh doanh, tiết kiệm chi phí bảo
                  trì sửa chữa, ...:
                </p>
                <p>
                  <i class="fa-solid fa-arrows-turn-right"></i> Quản lý tập
                  trung mọi tài sản trang thiết bị và tất cả thông tin hồ sơ lý
                  lịch thiết bị, lịch sử bảo trì sửa chữa, vận hành, nhà thầu,
                  ...
                </p>
                <p>
                  <i class="fa-solid fa-arrows-turn-right"></i> Số hóa toàn diện
                  quy trình nghiệp vụ bảo trì thiết bị: Bảo trì phòng ngừa (Bảo
                  dưỡng định kỳ, Lập kế hoạch bảo trì định kỳ); Bảo trì khắc
                  phục (Kiểm tra kiểm định thiết bị, Gửi yêu cầu bảo trì sự
                  cố,...); Công việc bảo trì (giao việc, thực hiện báo cáo kết
                  quả, trao đổi đánh giá kết quả)
                </p>
                <p>
                  <i class="fa-solid fa-arrows-turn-right"></i> Số hóa các
                  nghiệp vụ liên quan đến Công việc bảo trì: Quản lý kho vật tư
                  phụ tùng bảo trì, Quản lý mua sắm vật tư phục vụ bảo trì, Quản
                  lý ngân sách bảo trì
                </p>
              </div>
              <div className="image"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
