import React from "react";
import "./CollectData.scss";
import Button from "react-bootstrap/Button";
import AudioRecorder from "../Audio_Recorder/AudioRecorder";
import VideoRecorder from "../Audio_Recorder/VideoRecorder";

class CollectData extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      recordOption: "video",
    };
  }

  toggleRecordOption = (type) => {
    this.setState({ recordOption: type });
  };

  render() {
    const { recordOption } = this.state;
    return (
      <>
        <div className="collection-container">
          <div className="collection-title">COLLECTING DATA</div>
          <div className="collection-record">
            <div className="collection-button">
              <Button variant="dark" onClick={() => this.toggleRecordOption("video")}>Record Video</Button>
              <Button variant="dark" onClick={() => this.toggleRecordOption("audio")}>Record Audio</Button>
            </div>
            <h4>Docassemble section</h4>
            <div className="collection-type">
              {recordOption === "video" ? <VideoRecorder /> : <AudioRecorder />}
            </div>
            <div className="collection-docassemble">
            </div>          
          </div>
          <div className="collection-display"></div>
        </div>
      </>
    );
  }
}

export default CollectData;
