import React, { Component } from "react";
import Button from "react-bootstrap/Button";

const mimeType = "audio/webm";

class AudioRecorder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      permission: false,
      mediaRecorder: null,
      recordingStatus: "inactive",
      stream: null,
      audio: null,
      audioChunks: [],
    };
  }

  getMicrophonePermission = async () => {
    if ("MediaRecorder" in window) {
      try {
        const mediaStream = await navigator.mediaDevices.getUserMedia({
          audio: true,
          video: false,
        });
        this.setState({
          permission: true,
          stream: mediaStream,
        });
      } catch (err) {
        alert(err.message);
      }
    } else {
      alert("The MediaRecorder API is not supported in your browser.");
    }
  };

  startRecording = async () => {
    this.setState({
      recordingStatus: "recording",
    });
    const media = new MediaRecorder(this.state.stream, { type: mimeType });

    this.setState({
      mediaRecorder: media,
    });

    media.start();

    let localAudioChunks = [];

    media.ondataavailable = (event) => {
      if (typeof event.data === "undefined") return;
      if (event.data.size === 0) return;
      localAudioChunks.push(event.data);
    };

    this.setState({
      audioChunks: localAudioChunks,
    });
  };

  stopRecording = () => {
    this.setState({
      recordingStatus: "inactive",
    });
    this.state.mediaRecorder.stop();

    this.state.mediaRecorder.onstop = () => {
      const audioBlob = new Blob(this.state.audioChunks, { type: mimeType });
      const audioUrl = URL.createObjectURL(audioBlob);

      this.setState({
        audio: audioUrl,
        audioChunks: [],
      });
    };
  };

  render() {
    const { permission, recordingStatus, audio } = this.state;
    return (
      <div>
        <h2>Audio Recorder</h2>
        <main>
          <div className="audio-controls">
            {!permission ? (
              <Button variant="warning" onClick={this.getMicrophonePermission}>Get Microphone</Button>
            ) : null}
            {permission && recordingStatus === "inactive" ? (
              <Button variant="primary" onClick={this.startRecording}>Start Recording</Button>
            ) : null}
            {recordingStatus === "recording" ? (
              <Button variant="success" onClick={this.stopRecording}>Stop Recording</Button>
            ) : null}
          </div>
          {audio ? (
            <div className="audio-player">
              <audio src={audio} controls></audio>
              <a download href={audio}>
                <Button variant="info" style={{ marginTop: "20px" }}>Download Recording</Button>               
              </a>
            </div>
          ) : null}
        </main>
      </div>
    );
  }
}

export default AudioRecorder;
