import React, { Component } from "react";
import "./Doctor.scss";
import PrivatePage from "../../HOC/PrivatePage";
import Table from "react-bootstrap/Table";
import Button from 'react-bootstrap/Button';
import { NavLink } from "react-router-dom";

class Doctor extends Component {

  constructor(props) {
    super(props);
    this.state = {
      listUsers: [
        {id: "A1", name: "Tung", date: "01-08-2023"},
        {id: "A2", name: "Tri", date: "23-01-2022"},
        {id: "A3", name: "Tan", date: "01-13-2021"},
        {id: "A4", name: "Kha", date: "04-02-2020"},
        {id: "A5", name: "Toan", date: "09-27-20193"}
      ],
    };
  }


  render() {

    let list = this.state.listUsers;

    return (
      <div className="doctor-container">
        <div className="doctor-content">
          <Table striped bordered hover variant="success">
            <thead>
              <tr>
                <th>ID</th>
                <th>FullName</th>
                <th>Date of medical examination</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
            {list.map((item, index) => {
              return(
                <tr key={index}>
                  <td>{item.id}</td>
                  <td>{item.name}</td>
                  <td>{item.date}</td>
                  <td>
                  <Button variant="light">
                  <NavLink to="/report" className="nav-link">
                    Report
                  </NavLink></Button>
                  &nbsp; &nbsp; &nbsp;
                  <Button variant="light">
                  <NavLink to="/result" className="nav-link">
                    Result
                  </NavLink></Button>
                  </td>
                </tr>
              )
            })}
            </tbody>
          </Table>
          <Button variant="primary">Add</Button>
        </div>
      </div>
    );
  }
}

export default PrivatePage(Doctor);
