import React, { Component } from "react";
import { handleLogin } from "../../service/LoginService";
import "./Login.scss";
import { Redirect } from "react-router-dom";
import { toast } from "react-toastify";
import localStorage from "localStorage";

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: "",
      isShowPassword: true,
      errorMessage: "",
      isLoggedIn: false,
    };
  }

  handleInputUserName = (event) => {
    this.setState({
      username: event.target.value,
    });
  };

  handleInputPassword = (event) => {
    this.setState({
      password: event.target.value,
    });
  };

  handleShowPassword = () => {
    this.setState({
      isShowPassword: !this.state.isShowPassword,
    });
  };

  Login = async () => {
    try {
      let data = await handleLogin(this.state.username, this.state.password);
      if (data && data.accessToken) {
        toast.success("Login thành công");

        localStorage.setItem("token", data.accessToken);

        this.setState({
          isLoggedIn: true,
        });
        // setTimeout(() => {
        //   window.location.reload();
        // }, 1500);
        
      }
      else{
        toast.error("Login failed");
      }
    } catch (error) {
      if (error.response) {
        console.log(error.response);
        //toast.error(error.response.data.message);
      }
    }
  };

  render() {
    if (this.state.isLoggedIn) {
      return <Redirect to="/home" />;
    }
    return (
      <>
        <div className="login-background">
          <div className="login-container">
            <div className="login-content row">
              <div className="col-12 text-login">Login</div>
              <div className="col-12 form-group login-input">
                <label>Username: </label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Enter your username"
                  value={this.state.username}
                  onChange={(event) => this.handleInputUserName(event)}
                />
              </div>
              <div className="col-12 form-group login-input">
                <label>Password: </label>
                <div className="show-hidePassword">
                  <input
                    type={this.state.isShowPassword ? "password" : "text"}
                    className="form-control"
                    placeholder="Enter your password"
                    value={this.state.password}
                    onChange={(event) => this.handleInputPassword(event)}
                  />
                  <span
                    onClick={() => {
                      this.handleShowPassword();
                    }}
                  >
                    <i
                      class={
                        this.state.isShowPassword
                          ? "fa-regular fa-eye-slash"
                          : "fa-regular fa-eye"
                      }
                    ></i>
                  </span>
                </div>
              </div>
              <div className="col-12">
                <button className="btn-login" onClick={() => this.Login()}>
                  Login
                </button>
              </div>
              <div className="col-12">
                <span className="forgot-password">Forgot your password</span>
              </div>
              <div className="col-12">
                <span className="or-login">Or login with</span>
              </div>
              <div className="col-12 social-login">
                <i class="fa-brands fa-google-plus-g google"></i>
                <i class="fa-brands fa-facebook-f facebook"></i>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Login;
